.DailyGoals {
    min-height: 100vh;
    width: 100%;
    background-color: #fff;
    color: black;
    display: grid;
    grid-template: repeat(15, 40px) / repeat(3, 1fr);
    row-gap: 10px;
    
    
    // >>>>>>>>>>>>>>>  NAV AREA <<<<<<<<<<<<<<
    
    &__navPanel {
        grid-column: 1 / -1;
        grid-row: 1 / 2;
        padding: 35px 10px;
        display: grid;
        align-content: center;
        grid-template: 1fr / 1fr 2fr 1fr;
        font-size: 1.2rem;
        box-shadow: 0 0 15px rgba(128, 128, 128, 0.219);
    }
    &__navHeader {
        justify-self: center;
        font-size: 1.3rem;
        font-weight: 500;
    }
    &__navSave {
        justify-self: end;
        font-size: 1.1rem;
        padding-right: 10px;
        color: rgb(95, 167, 226);
    }
    &__backBox {
        color: rgb(95, 167, 226);
        justify-self: start;
        font-size: 1.5rem;
        margin-left: 10px;
    }


    &__header {
        grid-column: 1 / -1;
        grid-row: 2 / 5;
        justify-self: center;
        align-self: center;
        font-size: 1.4rem;
        padding-top: 15px;
    }



    &__caloriesTxt {
        grid-column: 1 / 3;
    }
    &__caloriesValueBox {
        grid-column: 3;
    }
    &__caloriesValueInput,
    &__proteinValueInput,
    &__fatValueInput,
    &__carbsValueInput,
    &__proteinPercInput,
    &__fatPercInput,
    &__carbsPercInput {
        width: 70%;
        height: 40px;
        margin-right: 5px;
        border-radius: 5px;
        border: 1px solid rgba(128, 128, 128, 0.26);
    }
    &__caloriesTxt,
    &__proteinTxt,
    &__fatTxt,
    &__carbsTxt {
        font-size: 1.1rem;
        align-self: center;
        margin-left: 10px;
    }

    &__helper {
        grid-column: 1 / -1;
        grid-row: 9 / 11;
        align-self: center;
        justify-self: center;
        font-size: 1.3rem;
        color:  rgb(95, 167, 226);
    }


    // >>>>>>>>>>>>>>>  SUMMARY AREA <<<<<<<<<<<<<<

    &__summaryBox {
        grid-column: 1 / -1;
        grid-row: 11 / 13;
        background-color: rgba(212, 212, 212, 0.13);
        display: grid;
        grid-template: 1fr 1fr / repeat(5, 1fr);
        padding: 5px 0 5px 15px;
    }
    &__summaryHeader {
        grid-column: 1 / 5;
        grid-row: 1;
        align-self: end;
        padding-bottom: 5px;
    }
    &__summaryTxt {
       grid-row: 2;
       grid-column: 1 / -1;
       font-size: .7rem;
       color: rgba(0, 0, 0, 0.363);
    }
    &__summaryPerc {
        grid-column: 5;
        grid-row: 1;
        align-self: center;
        justify-self: center;
        color: rgb(32, 185, 32);
        font-size: 1.2rem;
    }
}